import { setInfo, getInfo } from '@/utils/storage'
export default {
  namespaced: true,
  state () {
    return {
      // 提供数据
      // 个人权证相关
      userInfo: getInfo()
    }
  },
  mutations: {
    // 提供修改数据的方法
    // 所有mutations的第一个参数都是state
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    // 提供异步操作
    logout (context) {
      // 个人信息要重置
      context.commit('setUserInfo', {})
      // 购物车的信息要重置 (跨模块调用我们的mutation)  cart/setCartList
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {
    // 基于state所派生出来的属性
  }
}
